import React from 'react';

import AspectRatio from '@mui/joy/AspectRatio';
import Card from '@mui/joy/Card';
import CardContent from '@mui/joy/CardContent';
import CardOverflow from '@mui/joy/CardOverflow';
import Divider from '@mui/joy/Divider';
import IconButton from '@mui/joy/IconButton';
import Typography from '@mui/joy/Typography';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';

import Box from '@mui/joy/Box';
import Link from '../Link';
import Tag from '../Tag';
import { getPostUri } from '../../utils/route';

import * as style from './BlogArchive.module.less';

export interface PostType {
  readonly node: {
    readonly id: string;
    readonly excerpt: string;
    readonly frontmatter: {
      readonly title: string;
      readonly date: string;
      readonly cover: string;
      readonly tags: Array<string>;
    };
    readonly parent: {
      readonly relativePath: string;
    };
  };
}

interface BlogArchiveProps {
  posts: Array<PostType>;
}

export default (function ({ posts }) {
  return (
    <Box className={style.blogArchive}>
      {posts.map(({ node }) => {
        const slug = getPostUri(node.parent.relativePath);

        return (
          <Card className={style.blogArchiveCard} key={node.id} variant="outlined">
            <AspectRatio minHeight="160px" maxHeight="240px">
              <img src={node.frontmatter.cover} loading="lazy" alt="" />
            </AspectRatio>
            <CardContent>
              <Link className={style.blogArchiveCardLink} to={slug}>
                <h2 className={style.blogArchivePostTitle}>
                  {node.frontmatter.title}
                  <span className={style.blogArchivePostDate}>{node.frontmatter.date}</span>
                </h2>
              </Link>
              {node.excerpt}
            </CardContent>
            {node.frontmatter.tags?.length && (
              <CardOverflow className={style.blogArchiveCardExtended} variant="soft" sx={{ bgcolor: 'background.level1' }}>
                <Divider inset="context" />
                <CardContent className={style.blogArchiveCardExtendedContent}>
                  <Typography>{node.frontmatter.tags.map((name: string | null, index: number) => name && <Tag name={name} sx={{ ml: index ? 1 : 0 }}></Tag>)}</Typography>
                </CardContent>
              </CardOverflow>
            )}
          </Card>
        );
      })}
    </Box>
  );
} as React.FC<BlogArchiveProps>);
